.sidebar__divBig {
  background: white;
  height: 100vh;
  width: 100%;
  box-shadow: 6px 0px 18px rgba(0, 0, 0, 0.06);
  position: relative;
  z-index: 1;

  display: block !important;
}

.name {
  margin-left: 7.14px;
  font-size: 20px;
  font-weight: 700;
  color: #5c5c5c;
  position: absolute;
  top: 55%;
}
.logo__header {
  position: relative;
  padding-top: 24px;
  padding-left: 26px;
}
.avater__div {
  margin-top: 32px;
  margin-bottom: 43px;
}
.user__name {
  font-size: 12px;
  font-weight: 500;
  margin-top: 5px;
  color: black;
}
.name__and__email {
  padding-left: 9px !important;
}
.user__email {
  margin-top: 2px;
  font-size: 11px;
  font-weight: 500;
  color: #90a0b7;
}
.user__image {
  padding-left: 26px !important;
  margin-right: 9px;
}
.route__list {
  font-size: 13px !important;
  font-weight: 500;
  color: #334d6e;
  margin-bottom: 20px;
  cursor: pointer;
}
.route__list:hover {
  color: #1941cf !important;
}
.list__icon {
  font-size: 15px !important;
  margin-right: 15.5px;
  margin-left: 26.5px;
  color: #c2cfe0;
}
.no__advater__div {
  width: 40px;
  height: 40px !important;
  background: silver;
  border-radius: 100%;
  position: absolute;
  top: 0% !important;
  text-align: center;
  padding-top: 20%;
}
@media (max-width: 500px) {
  .side__bar__cover {
    width: 70% !important;
  }
  .sidebar__div {
    background: white;
    height: 100vh;
    width: 100%;
    box-shadow: 6px 0px 18px rgba(0, 0, 0, 0.06);
    position: absolute;
    z-index: 1;
  }
  .sidebar__divBig {
    display: none !important;
  }
}
