.publisher__main__cover {
  background-color: white;
  border-radius: 4px;
  padding-top: 50px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 50px;
}
.file__image__div {
  border: 1px solid #6019cf !important;
  padding: 0px !important;
  height: 150px;
  position: relative;
}
.label__image__icon {
  position: absolute;
  top: 40%;
  left: 40%;
  cursor: pointer;
}
.input__cover__div {
  padding: 0px !important;
  padding-left: 50px !important;
  padding-right: 50px !important;
}
.published__input {
  width: 100%;
  margin-bottom: 20px;
  border-radius: 100px;
  border: 1px solid #6019cf !important;
  font-size: 14px !important;
  height: 40px !important;
  padding-left: 20px !important;
  padding-top: 10px !important;
}
#exampleFormControlTextarea1 {
  padding-left: 20px !important;
  font-size: 14px !important;
  padding-top: 10px !important;
  border: 1px solid #6019cf !important;
}
.pdf__upload__div__cover {
  width: 100%;
  height: 200px;
  margin-top: 20px;
  border-style: dashed;
  border-color: #6019cf !important ;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  position: relative;
}
.image__pdf__icon {
  transform: rotate(270deg);
}
.alert__cover {
  margin-top: 10px;
  margin-bottom: 10px;
}
.label__with__image {
  width: 100%;
  height: 100%;
  top: 0%;
  left: 0%;
}
.save__button {
  margin-top: 20px !important;
  width: 100% !important;
  color: white !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  border-radius: 26px !important;
  background: #6019cf !important;
  font-size: 14px !important;
}

@media (max-width: 500px) {
  .publisher__main__cover {
    background-color: white;
    border-radius: 4px;
    padding-top: 50px;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 50px;
  }
}
