.routerPage__cover {
  background: whitesmoke;
  padding-left: 44px !important;
  padding-top: 15px !important;
  padding-right: 75px !important;
  object-fit: contain;
}
.side__bar__cover {
  padding: 0px !important;
}
.form-control {
  border: 1px solid #6b44d8 !important;
}
.book__cat {
  background: white !important;
  margin-right: 16px !important;
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 16px;
  padding-bottom: 11px;
  overflow-y: scroll;
  height: 65vh;
}
.sales__chat {
  background: white !important;
}
.date__upload {
  font-size: 15px !important;
  font-weight: 500 !important;
  color: #192a3e !important;
  margin-bottom: 11px;
}
.icon__div {
  display: none;
}
@media (max-width: 500px) {
  .icon__div {
    display: block;
  }
  .routerPage__cover {
    background: #e5e5e5;
    padding-left: 0px !important;
    padding-top: 15px !important;
    padding-right: 0px !important;
  }
}
