.regCover {
  height: 100vh;
  background-image: url("../../images/cawadi-background.png");
  width: 100%;
  padding-bottom: 20px;
  object-fit: contain;
}
.alert__div__cover {
  margin-top: 10px;
  margin-bottom: 10px;
}
.reg__form__cover {
  padding-top: 119px;
}
.cont__logo {
  font-weight: 700;
  color: #5c5c5c;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 3px;
}
.sub__heading {
  display: flex;
  justify-content: center;
  color: #5c5c5c;
  font-size: 14px;
  margin-bottom: 29px;
}
.site__name {
  line-height: 40px;
}
.logo__image {
  object-fit: contain;
  width: 31px;
  height: 30px;
}
.reg__input__form {
  height: 45px !important;
  border-radius: 100px !important;
  border: none !important;
  font-size: 14px !important;
  color: #5c5c5c !important;
  font-weight: 400 !important;
  padding-left: 20px !important;
  display: flex !important;
  align-self: center !important;
}
.have__an__account {
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
  color: #5c5c5c;
}
.sign__in__option {
  color: #6c47d9;
  margin-left: 3px;
  cursor: pointer;
}
.form__div {
  margin-bottom: 9px !important;
  position: relative;
}
.button__div {
  margin-top: 24px !important;
}
.sign__up__button {
  text-transform: inherit !important;
  width: 100% !important;
  border-radius: 100px !important;
  background: #6019cf 1.55% !important;
  color: white !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  font-size: 14px !important;
  font-weight: 400 !important;
}
.visible {
  position: absolute;
  top: 0%;
  left: 90%;
  top: 30%;
  font-size: 14px !important;
  cursor: pointer;
  color: #5c5c5c;
}
.forgot__password {
  font-size: 12px !important;
  color: #5c5c5c;
  font-weight: 400 !important;
}
.form-control {
  border: 1px solid #6b44d8 !important;
}
